$cell-size: 100px;
$cell-count: 7;
$gap: 1px;
$calendar-padding: 10px;
$calendar-days: 30;

$days:
  "mon" 0,
  "tue" $cell-size + $gap,
  "wed" ($cell-size + $gap) * 2,
  "thu" ($cell-size + $gap) * 3,
  "fri" ($cell-size + $gap) * 4,
  "sat" ($cell-size + $gap) * 5,
  "sun" ($cell-size + $gap) * 6;
